import { DataExchangeService } from "services/DataExchangeService";

let generatedComment = "";
export async function scrapePost() {
    console.log("‚è≥ [Scraper] Waiting for DOM to load...");

    if (document.readyState === "loading") {
        await new Promise((resolve) => document.addEventListener("DOMContentLoaded", resolve));
        console.log("‚úÖ [Scraper] DOM fully loaded.");
    } else {
        console.log("‚úÖ [Scraper] DOM was already ready.");
    }

    const postSelector = '.update-components-update-v2__commentary span[dir="ltr"]';

    const postText = await waitForPostText(postSelector);

    if (postText) {
        return postText;

    } else {
        console.error("‚ùå [Scraper] Failed to find or extract post text.");
        throw new Error("post could not be extracted");
    }
}

function waitForPostText(selector: string, timeout = 10000): Promise<string | null> {
    return new Promise((resolve) => {
        const startTime = Date.now();

        const interval = setInterval(() => {
            const elapsed = Date.now() - startTime;
            const element = document.querySelector(selector) as HTMLElement;

            if (element) {
                console.log("‚úÖ [Scraper] Found post text element.");
                clearInterval(interval);
                resolve(element.innerText.trim());
            }

            if (elapsed >= timeout) {
                console.warn("‚ö†Ô∏è [Scraper] Timeout: Post text not found.");
                clearInterval(interval);
                resolve(null);
            }
        }, 500);
    });
}